
import {mapState,mapGetters,mapMutations,mutationAccesstoken} from 'vuex';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
name: 'app',
data:function(){
return{

}
},
components:{
Tab,
TabItem,
XHeader,
Actionsheet
},
mounted:function(){
var loginStatus= window.localStorage.getItem('loginStatus');
if(loginStatus){

this.mutationIsLogin();
this.mutationLoginname(window.localStorage.getItem('loginname'));

}
},
methods:{
...mapMutations(['mutationIsLogin','mutationLoginname','mutationAccesstoken']),
handleClickMoreMenu(menuKey,menuItem){
this.$router.push('/'+menuKey);
},

showMoreMenus(){ //
this.isShowMore=true;
}
},
computed:{
...mapGetters(['moreMenus']),
...mapState(['isLogined','unLoginedMoreMenus','LoginedMoreMenus']),
isShowMore:{
get:function(){return this.$store.state.isShowMore},
set:function(nval){this.$store.commit('mutationIsShowMore',nval)}
},


}
}



