import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState} from 'vuex';
export default {
name: "topiclist",
computed:{
...mapState(['topics'])
},
props: ["name"],
components: {
Cell, Group, Scroller, Tab, TabItem
},
methods:{
routeto(index){

console.log(index);
this.$router.push('/topiclist/'+this.topics[index].name);
},

freshData(){//
var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){
