loadingContent:'.....'
}
};
},



beforeRouteEnter: function(to, from, next) {




next(function(vm) {

vm.$http
.get("/topics", {
params: {
tab: to.params.name, //
page: vm.page,
limit: vm.limit
}
})
.then(function(res) {
console.log(res.data.data);
if (res.data.success) {
vm.lists = res.data.data;
}
});
});
},
beforeRouteUpdate: function(to, from, next) {

var app = this;
this.$http
.get("/topics", {
params: {
tab: to.params.name,
page: this.page,
limit: this.limit
}
})
.then(function(res) {
if (res.data.success) {
app.lists = res.data.data;
}
});
next();
}
};



